import React from 'react';
import { Helmet } from 'react-helmet';
import { useInView } from 'react-intersection-observer';
import './Home.css';
import Header from '../../components/header/header';
import Footer from '../../components/footer/Footer';

function Section({ className, title, content }) {
    const [ref, inView] = useInView({
        triggerOnce: true,  // Only trigger this effect once
        threshold: 0.1,     // Percentage of the element that's visible before triggering
    });

    return (
        <section ref={ref} className={`section ${className} ${inView ? 'is-visible' : ''}`}>
            <h2>{title}</h2>
            <p>{content}</p>
        </section>
    );
}

function Home() {
    return (

        <div className="home-container">
            <Helmet>
                <title>  </title>
            </Helmet>

            <Header />
            <main className="home-content">
                <Section className="introduction" title=" " content="!" />
                <Section className="new-info-protection" title="     ." content="   .  []   !" />
                <Section className="why-use-name" title="  " content="  1000 ,  100  .   3!" />
                <Section className="some-content" title="" content="" />
            </main>
            <Footer />
        </div>
    );
}

export default Home;
