import React from "react";
import './InspectionSteps.css';
function InspectionSteps({active}){
    return (
        <div className="inspection-steps">
            <ol>
                <li className={active === "first" ? "active" : ""}>  </li>
                <li className={active === "second" ? "active" : ""}> </li>
                <li className={active === "third" ? "active" : ""}></li>
                <li className={active === "fourth" ? "active" : ""}> </li>
            </ol>
        </div>
    );
}

export default InspectionSteps;